<template>
    <div class="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50">
        <div class="w-full max-w-lg p-6 bg-slate-50 text-card-foreground rounded-md">
            <div class="flex flex-col items-center">
                <div class="mb-4 rounded-full bg-muted p-4">
                    <img
                        aria-hidden="true"
                        alt="avatar placeholder"
                        src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTzRN-MktYXqGZtPc5k_F8xXbopQZ5n1ZNfqg&s"
                    />
                </div>
                <h2 class="mb-2 text-lg font-semibold text-center text-blue-900">¿Desea desactivar este perfil?</h2>
                <div class="text-center text-muted-foreground">
                    <p class="font-medium text-primary">{{ infoMedico.nombre }}</p>
                    <p class="text-sm">{{ infoMedico.especialidad }} | Documento {{ infoMedico.documento }}-{{ infoMedico.direccion.estado }}</p>
                    <p class="mt-4 text-sm">Al desactivar este perfil, sus informaciones quedaran inactivas para nuevas consultas.</p>
                    <p class="mt-2 text-sm">Certifique que no hay consultas agendadas, caso tenga, la desactivación no podra ser concluída.</p>
                </div>
            </div>
            <div class="mt-6 flex flex-col space-y-2">
                <button
                    @click="desactivarPerfil(medico)"
                    class="border-2 py-2 px-4 rounded-md focus:outline-none focus:ring-2 bg-[#072b52] text-white hover:bg-blue-900 active:border-blue-900 active:text-blue-900 active:bg-white active:border-2"
                >
                    Desactivar este perfil
                </button>
                <button
                    @click="$emit('close')"
                    class="py-2 px-4 border-2 rounded-md focus:outline-none focus:ring-2 border-[#003366] hover:bg-primary/10 text-[#003366] active:bg-[#003366] active:text-white"
                >
                    Cancelar
                </button>
            </div>
        </div>
    </div>
</template>

<script setup lang="ts">
import { reactive, defineProps, defineEmits } from 'vue';
import { Medico } from '../../login/interfaces/Medico';
import MedicoService from '../../login/helper/MedicoService';
import Swal from 'sweetalert2';

const props = defineProps<{
    medico: Medico;
}>();
const infoMedico = reactive<Medico>({ ...props.medico });
const emit = defineEmits(['medicDeleted', 'close']);

async function desactivarPerfil(medico: Medico) {
    try {
        const response = await MedicoService.desactivarMedico(medico);
        Swal.fire({
            icon: 'success',
            title: 'Médico Desactivado',
            text: response.data,
        });
        emit('medicDeleted', medico);
        emit('close');
    } catch (error) {
        console.error('error', error);
    }
}
</script>

<style scoped></style>
